@keyframes starfield {
  from {
    transform: translateX(0px);
  }

  to {
    transform: translateX(-2000px);
  }
}

@media (min-width: 2000px) {
  @keyframes starfield {
    from {
      transform: translateX(0vw);
    }

    to {
      transform: translateX(-200vw);
    }
  }
}

.scene {
  width: 100%;
  height: 100vh;
  position: absolute;
  top: 40%;
  left: 55%;
  -webkit-transform: translate(-50%, -50%) scale(1, 1);
  -moz-transform: translate(-50%, -50%) scale(1, 1);
  -ms-transform: translate(-50%, -50%) scale(1, 1);
  -o-transform: translate(-50%, -50%) scale(1, 1);
  transform: translate(-50%, -50%) scale(1, 1);
}

.comet {
  width: 120%;
  height: 3px;
  position: fixed;
  top: 30%;
  left: 50%;
  -webkit-transform: translate(-50%, -50%) rotate(-10deg);
  -moz-transform: translate(-50%, -50%) rotate(-10deg);
  -ms-transform: translate(-50%, -50%) rotate(-10deg);
  -o-transform: translate(-50%, -50%) rotate(-10deg);
  transform: translate(-50%, -50%) rotate(-10deg);
}

.comet .comet-inner {
  width: 300px;
  height: 100%;
  display: block;
  position: absolute;
  left: 50%;
  -webkit-animation: zip 12s linear infinite;
  -moz-animation: zip 12s linear infinite;
  animation: zip 12s linear infinite;
}

@-moz-keyframes zip {
  0% {
    left: 100%;
  }

  85% {
    left: 100%;
  }

  90% {
    left: -50%;
  }

  100% {
    left: -50%;
  }
}

@-webkit-keyframes zip {
  0% {
    left: 100%;
  }

  85% {
    left: 100%;
  }

  90% {
    left: -50%;
  }

  100% {
    left: -50%;
  }
}

@keyframes zip {
  0% {
    left: 100%;
  }

  85% {
    left: 100%;
  }

  90% {
    left: -50%;
  }

  100% {
    left: -50%;
  }
}

.comet .comet-inner:before {
  content: "";
  background-color: #fff;
  width: 3px;
  height: 3px;
  position: absolute;
  left: 0%;
  border-radius: 100%;
}

.comet .comet-inner:after {
  content: "";
  background-color: rgba(255, 255, 255, 0.5);
  background-image: -webkit-linear-gradient(90deg, rgba(255, 255, 255, 0.5), #2e2d4d);
  background-image: -moz-linear-gradient(90deg, rgba(255, 255, 255, 0.5), #2e2d4d);
  background-image: -ms-linear-gradient(90deg, rgba(255, 255, 255, 0.5), #2e2d4d);
  background-image: -o-linear-gradient(90deg, rgba(255, 255, 255, 0.5), #2e2d4d);
  background-image: linear-gradient(90deg, rgba(255, 255, 255, 0.5), #2e2d4d);
  width: 200px;
  height: 3px;
  position: absolute;
  left: 0%;
  border-top-left-radius: 3px;
  border-top-right-radius: 100%;
  border-bottom-left-radius: 3px;
  border-bottom-right-radius: 100%;
}

@media screen and (max-height: 880px) {
  .scene {
    top: 45%;
  }
}

@media screen and (max-height: 820px) {
  .scene {
    top: 50%;
  }
}

@media screen and (max-height: 720px) {
  .scene {
    top: 55%;
  }
}

@media screen and (max-height: 650px) {
  .scene {
    top: 60%;
  }
}