.emptyList {
  width: 100%;
}

.emptyList-text {
  font-family: 'Poppins';
  color: #fff;
  font-weight: 300;
  font-size: 36px;
  text-align: center;
  position: absolute;
  bottom: 30px;
  z-index: 100;
  left: 50%;
  transform: translateX(-50%);
  text-shadow: 0px 0px 10px #00ccff;
  max-width: 600px;
  width: 100%;
  padding: 0 15px;
}

.emptyList-text--back {
  text-decoration: underline;
}

.emptyList-img {
  position: absolute;
  bottom: 0;
  left: 50%;
  transform: translateX(-50%);
  width: 650px;
}


@media (max-width: 580px) {
  .emptyList-text{
    font-size: 24px;
  }
  .emptyList-img{
    width: 100%;
  }
}


@media (max-width: 380px) {
  .emptyList-text{
    font-size: 20px;
  }
}